import { ViewController } from 'ionic-angular/navigation/view-controller';
import { Utility } from './../../../services/utility';
import { ContactProvider } from './../../../providers/contact-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { SqliteProvider } from './../../../providers/sqlite-provider';
/**
 * Generated class for the ContactSetremarkPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contact-setremark',
  templateUrl: 'contact-setremark.html',
})
export class ContactSetremarkPage {

  contact: any;
  aliasName:string;

  isReadyToSave: boolean = false;
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl:ViewController,
    public events:Events,
    public contactProvider: ContactProvider,
    public sqliteProvider:SqliteProvider) {
    this.contact = this.navParams.data;
    this.aliasName = this.contact.aliasName; 
    this.events.subscribe('contact: refresh-friendtag',(data) =>{
      //console.log(data)
      this.sqliteProvider.queryFriendInfo(data.id).subscribe();
    }) 
  }

  ionViewDidLoad() { 
  }

  ionViewWillUnload(){
    this.events.unsubscribe('contact: refresh-friendtag')
  }

  AliasNameChanged(){ 
    if(this.contact.aliasName != this.aliasName){
      this.isReadyToSave = true;
    }else {
      this.isReadyToSave = false;
    } 
  }

  saveSetting( ){
    this.contactProvider.FriendSetting(this.contact.id,
      [{
        "Name":"aliasname",
        "value":this.aliasName
     }]
      ).subscribe(res=>{        
        //更新sql表
        this.sqliteProvider.updateFriendInfo(this.contact).subscribe();
    }) 
    this.viewCtrl.dismiss();
  }

  openFriendTagList(){
    this.navCtrl.push("ContactTagChooserPage",this.contact);
  }
}



